:root {
  --c-white: #ffffff;
  --c-black: #000000;
  --c-dark: #333;
  --c-grey: #6f6f6f;
  --c-blue: #4c74d3;
  --admin-bar-height: 0;
}

/* ====== GLOBAL STYLES  ====== */
*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  direction: rtl;
  text-align: right;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  line-height: 1.6;
  color: #929292;
  unicode-bidi: embed;
  background-color: #fff;
}

@media (max-width: 991px) {
  body {
    font-size: 16px;
  }
}

::selection {
  color: #fff;
  background: #333;
  text-shadow: none;
}

a {
  transition: 0.2s all;
  text-decoration: none;
  color: var(--c-dark);
  font-weight: 500;
  font-size: 15px;
}

a:hover {
  color: var(--c-primary);
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  margin: 0 0 20px;
  color: #333;
}

@media (max-width: 991px) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 15px;
  }
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
}

h1 {
  font-size: 35px;
  line-height: 1.3;
}

@media (max-width: 991px) {
  h1 {
    font-size: 30px;
  }
}

h2 {
  font-size: 30px;
  line-height: 1.35;
}

@media (max-width: 991px) {
  h2 {
    font-size: 25px;
  }
}

h3 {
  font-size: 24px;
  line-height: 1.5;
}

@media (max-width: 991px) {
  h3 {
    font-size: 21px;
  }
}

h4 {
  font-size: 20px;
  line-height: 1.6;
}

@media (max-width: 991px) {
  h4 {
    font-size: 18px;
  }
}

h5 {
  font-size: 18px;
  line-height: 1.6;
}

@media (max-width: 991px) {
  h5 {
    font-size: 16px;
  }
}

h6 {
  font-size: 16px;
  line-height: 1.6;
}

@media (max-width: 991px) {
  h6 {
    font-size: 14px;
  }
}

p,
ul,
ol {
  margin: 0 0 20px;
}

@media (max-width: 991px) {
  p,
  ul,
  ol {
    margin-bottom: 15px;
  }
}

ol,
ul {
  padding-inline-start: 1.2em;
}

blockquote {
  font-style: italic;
  margin: 0 0 20px;
}

@media (max-width: 991px) {
  blockquote {
    margin-bottom: 15px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

/* =WordPress Core
-------------------------------------------------------------- */
.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  /* Image does not overflow the content area */
  padding: 5px 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

.gallery-caption {
  font-size: 0.9em;
}

.bypostauthor {
  font-style: italic;
}

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
  background-color: #eee;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

#wpadminbar {
  position: fixed;
}

.t-center {
  text-align: center;
}

.t-right {
  text-align: right;
}

.t-left {
  text-align: left;
}

input[type="text"],
input[type="email"],
input[type="search"],
textarea {
  max-width: 100%;
  padding: 5px 15px;
  font-size: 15px;
  box-shadow: none;
  border: 1px solid var(--c-grey);
  display: block;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="search"]:focus,
textarea:focus {
  border-color: var(--c-blue);
  outline: 0;
}

textarea {
  height: 60px;
  resize: none;
}

label {
  max-width: 100%;
  font-size: 14px;
}

input[type="submit"] {
  -webkit-appearance: none;
  padding: 10px;
  margin: 0;
  border: 1px solid var(--c-blue);
  color: var(--c-white);
  background-color: var(--c-blue);
  font-size: 18px;
  display: block;
  box-shadow: none;
  outline: 0;
  transition: 0.35s;
}

input[type="submit"]:hover {
  opacity: 0.8;
}

.js-bg {
  width: 1px;
  height: 1px;
  opacity: 0;
}

.s-back-switch {
  background-position: 50%;
  background-size: cover;
}

/* ====== PAGE TEMPLATE STYLES  ====== */
.hr-page {
  min-height: calc(100vh - var(--admin-bar-height));
  display: flex;
  flex-direction: column;
}

.hr-section {
  padding-top: calc(30px + 2.5vw);
}

.hr-section:last-of-type {
  padding-bottom: calc(30px + 2.5vw);
}

.hr-section__title {
  margin-bottom: calc(20px + 1.5vw);
  font-size: 30px;
  text-align: center;
}

@media (max-width: 991px) {
  .hr-section__title {
    font-size: 24px;
  }
}

.hr-header {
  position: sticky;
  background-color: var(--c-blue);
  width: 100%;
  left: 0;
  top: var(--admin-bar-height);
  z-index: 100;
}

.hr-header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}

.hr-header__bar-wrap {
  font-size: 14px;
}

.hr-header__bar-wrap a {
  color: #fff;
  font-size: inherit;
}

.hr-header__bar-wrap a:hover {
  opacity: 0.7;
}

.hr-header__bar-wrap a i {
  margin-left: 0.33em;
}

@media (min-width: 1025px) {
  .hr-header .menu-primary-menu-container {
    padding-top: 15px;
    margin-top: 15px;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
  .hr-header .menu {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    justify-content: space-between;
  }
  .hr-header .menu i {
    display: none;
  }
  .hr-header .menu .menu-item-has-children {
    position: relative;
  }
  .hr-header .menu .menu-item-has-children:hover > .sub-menu {
    opacity: 1;
    visibility: visible;
  }
  .hr-header .menu > li > a {
    display: block;
    width: 100%;
    color: var(--c-white);
    font-size: 18px;
    position: relative;
  }
  .hr-header .menu > li > a::after {
    display: inline-block;
    content: '';
    position: absolute;
    left: 0;
    bottom: -15px;
    width: 100%;
    height: 4px;
    background-color: currentColor;
    opacity: 0;
    transition: 0.35s opacity;
  }
  .hr-header .menu > li.current-menu-item > a::after, .hr-header .menu > li:hover > a::after {
    opacity: 1;
  }
  .hr-header .sub-menu {
    position: absolute;
    background-color: var(--c-white);
    list-style: none;
    margin: 0;
    top: calc(100% + 15px);
    min-width: 180px;
    padding: 5px 0;
    opacity: 0;
    visibility: hidden;
    transition: 0.35s;
    box-shadow: 0 0 3px 2px rgba(76, 116, 211, 0.2);
  }
  .hr-header .sub-menu a {
    font-size: 18px;
    display: block;
    padding: 5px 15px;
    line-height: 1.2;
  }
  .hr-header .sub-menu > li:hover > a {
    color: var(--c-white);
    background-color: var(--c-blue);
  }
  .hr-header .sub-menu .sub-menu {
    top: 0;
    right: 100%;
  }
  .hr-header__nav {
    max-width: 75%;
    padding-right: 50px;
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  .hr-header__bar-wrap {
    display: flex;
    justify-content: space-between;
    order: -1;
  }
  .hr-header__logo img {
    max-height: 80px;
    width: auto;
  }
  .hr-header__logo__burger {
    display: none;
  }
}

@media (max-width: 1024px) {
  .hr-header .container {
    max-width: none;
  }
  .hr-header .menu-primary-menu-container ul {
    list-style: none;
    padding-right: 0;
  }
  .hr-header .menu-primary-menu-container .menu {
    margin-bottom: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  }
  .hr-header .menu-primary-menu-container .menu > li > a {
    font-size: 18px;
  }
  .hr-header .menu-primary-menu-container .menu i {
    color: #fff;
    position: absolute;
    left: 0;
    top: 0;
    height: 46px;
    width: 46px;
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
  }
  .hr-header .menu-primary-menu-container .menu i::before {
    display: block;
    transition: 0.35s;
  }
  .hr-header .menu-primary-menu-container .menu li {
    position: relative;
  }
  .hr-header .menu-primary-menu-container .menu li.is-opened > i::before {
    transform: rotate(-90deg);
  }
  .hr-header .menu-primary-menu-container .menu .menu-item-has-children > a {
    padding-left: 50px;
  }
  .hr-header .menu-primary-menu-container .menu a {
    color: var(--c-white);
    font-size: 16px;
    padding: 10px 30px;
    display: block;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    line-height: 24px;
  }
  .hr-header .menu-primary-menu-container .menu a:hover {
    opacity: 0.7;
  }
  .hr-header .menu-primary-menu-container .sub-menu {
    display: none;
  }
  .hr-header .menu-primary-menu-container .sub-menu a {
    padding-right: 45px;
  }
  .hr-header .menu-primary-menu-container .sub-menu .sub-menu a {
    padding-right: 60px;
  }
  .hr-header__nav {
    position: fixed;
    right: 0;
    top: var(--admin-bar-height);
    transform: translateX(100%);
    height: calc(100vh - var(--admin-bar-height));
    background-color: var(--c-blue);
    width: 270px;
    padding: 50px 0 30px;
    overflow: auto;
    transition: 0.4s transform;
    display: block;
  }
  .is-opened .hr-header__nav {
    transform: translateX(0);
  }
  .hr-header__overlay {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    opacity: 0;
    visibility: hidden;
    transition: 0.4s opacity, 0.4s visibility;
  }
  .is-opened .hr-header__overlay {
    opacity: 1;
    visibility: visible;
  }
  .hr-header .menu-primary-menu-container {
    margin-top: 0;
    padding-top: 0;
    border-top: 0;
    margin-bottom: 50px;
  }
  .hr-header__bar-wrap {
    padding-right: 30px;
    padding-left: 30px;
  }
  .hr-header__bar-wrap > div {
    margin-top: 15px;
  }
  .hr-header__logo img {
    max-height: 50px;
  }
  .hr-header__burger {
    width: 40px;
    height: 28px;
    position: relative;
  }
  .hr-header__burger span {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 3px;
    background-color: var(--c-white);
    display: block;
    transition: 0.2s 0.2s background-color;
  }
  .hr-header__burger span::after, .hr-header__burger span::before {
    position: absolute;
    left: 0;
    width: 100%;
    display: block;
    content: '';
    background-color: var(--c-white);
    height: 3px;
  }
  .hr-header__burger span::before {
    top: -12px;
    transition: 0.2s 0.2s top, 0.2s transform;
  }
  .hr-header__burger span::after {
    bottom: -12px;
    transition: 0.2s 0.2s bottom, 0.2s transform;
  }
  .hr-header__burger.is-opened span {
    background-color: transparent;
    transition: 0.2s background-color;
  }
  .hr-header__burger.is-opened span::before {
    top: 0;
    transform: rotate(45deg);
    transition: 0.2s top, 0.2s 0.2s transform;
  }
  .hr-header__burger.is-opened span::after {
    bottom: 0;
    transform: rotate(-45deg);
    transition: 0.2s bottom, 0.2s 0.2s transform;
  }
}

@media (max-width: 475px) {
  .hr-header__nav {
    width: calc(100% - 70px);
    max-width: none;
  }
}

.hr-footer {
  background-color: var(--c-blue);
  padding: 15px 0;
  margin-top: auto;
}

.hr-footer__wrap p,
.hr-footer__wrap ul,
.hr-footer__wrap ol,
.hr-footer__wrap a {
  color: #fff;
}

.hr-footer__wrap p {
  margin-bottom: 0;
}

.hr-footer__wrap a {
  font-weight: bold;
}

.hr-footer__wrap a:hover {
  opacity: 0.7;
}

/* ====== PRELOADER STYLES ====== */
/* ====== 404 PAGE STYLES  ====== */
/* ====== SHORTCODE SECTION ====== */
.hr-cards__item {
  text-align: center;
}

.hr-cards__item img {
  height: 150px;
  width: 150px;
  object-fit: contain;
}

@media (max-width: 767px) {
  .hr-cards__item img {
    height: 110px;
    width: 110px;
  }
}

.hr-cards__item {
  margin-bottom: 30px;
}

.hr-cards__list {
  margin-bottom: -30px;
}

.hr-cards__description {
  margin-bottom: 0;
  margin-top: 15px;
  font-size: 30px;
  font-weight: normal;
  line-height: 1.2;
}

@media (max-width: 767px) {
  .hr-cards__description {
    font-size: 24px;
  }
}

.hr-banner .swiper-slide {
  background-size: cover;
  background-position: 50%;
  height: 60vw;
  min-height: 320px;
  max-height: 600px;
  padding-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  padding-bottom: 70px;
}

.hr-banner .swiper-slide::before {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  content: '';
  display: block;
}

.hr-banner .container {
  position: relative;
}

.hr-banner h2 {
  font-size: calc(30px + 2.1vw);
}

.hr-banner h6 {
  font-size: 24px;
  margin-bottom: 0;
  font-weight: normal;
}

@media (max-width: 991px) {
  .hr-banner h6 {
    font-size: 21px;
  }
}

@media (max-width: 768px) {
  .hr-banner h6 {
    font-size: 18px;
  }
}

.hr-banner h2,
.hr-banner h6 {
  color: var(--c-white);
}

.hr-banner .swiper-button-prev,
.hr-banner .swiper-button-next {
  background: none;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid var(--c-white);
  margin-top: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  color: var(--c-white);
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 20px;
  transition: 0.3s;
}

@media (max-width: 767px) {
  .hr-banner .swiper-button-prev,
  .hr-banner .swiper-button-next {
    display: none;
  }
}

.hr-banner .swiper-button-prev:hover,
.hr-banner .swiper-button-next:hover {
  background-color: var(--c-white);
  color: var(--c-dark);
}

.hr-banner .swiper-button-prev {
  right: 30px;
}

.hr-banner .swiper-button-next {
  left: 30px;
}

.hr-banner .swiper-pagination-bullet {
  background-color: var(--c-white);
}

.hr-banner .swiper-pagination-bullet-active {
  background-color: var(--c-white);
}

.hr-services__wrap {
  display: flex;
  margin-left: -15px;
  margin-right: -15px;
  align-items: center;
}

@media (max-width: 991px) {
  .hr-services__wrap {
    align-items: baseline;
    flex-direction: column;
  }
}

.hr-services__image {
  flex: 0 0 33.33%;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 991px) {
  .hr-services__image {
    flex: 0 0 100%;
    margin-bottom: 15px;
  }
}

.hr-services__list {
  padding: 15px;
  padding-left: 7.5%;
  flex: 0 0 66.67%;
  list-style: none;
  margin-bottom: 0;
}

@media (max-width: 1200px) {
  .hr-services__list {
    padding-left: 15px;
  }
}

@media (max-width: 991px) {
  .hr-services__list {
    flex: 0 0 100%;
  }
}

.hr-services__list li {
  font-size: 26px;
  display: inline-block;
  vertical-align: top;
  margin-left: 30px;
  margin-bottom: 0.3em;
}

.hr-services__list li i {
  font-size: 1.3em;
  margin-left: 0.45em;
  color: var(--c-blue);
}

.hr-popup {
  position: fixed;
  width: 450px;
  max-width: calc(100% - 30px);
  background-color: var(--c-white);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 300;
  padding: 25px;
  opacity: 0;
  margin-top: 30px;
  visibility: hidden;
  transition: 0.35s;
}

.hr-popup.is-opened {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
}

.hr-popup__bg {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 299;
  opacity: 0;
  visibility: hidden;
  transition: 0.35s;
}

.is-opened + .hr-popup__bg {
  opacity: 1;
  visibility: visible;
}

.hr-popup__close {
  position: absolute;
  left: 25px;
  top: 25px;
  color: var(--c-dark);
  font-size: 20px;
}

.hr-popup__close:hover {
  color: var(--c-blue);
}

.hr-popup .wpcf7 {
  direction: rtl;
}

.hr-popup .wpcf7 .wpcf7-form-control-wrap {
  display: block;
}

.hr-popup .wpcf7 label {
  display: block;
}

.hr-popup .wpcf7 input,
.hr-popup .wpcf7 textarea {
  display: block;
  width: 100%;
}

.hr-popup .wpcf7 input[type="submit"] {
  margin-top: 25px;
}

.hr-popup .wpcf7 p {
  margin-bottom: 15px;
  position: relative;
}

.hr-popup .wpcf7 p:last-child {
  margin-bottom: 0;
}

.hr-popup div.wpcf7 .ajax-loader {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}

.hr-popup .wpcf7-response-output {
  font-size: 14px;
  margin: 0;
}

.hr-recent__list {
  margin-bottom: -30px;
}

.hr-recent__post {
  position: relative;
  min-height: 250px;
  padding: 15px;
  margin-bottom: 30px;
}

.hr-recent__link {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.hr-recent__categories {
  position: relative;
}

.hr-recent__categories a {
  background-color: var(--c-blue);
  color: var(--c-white);
  border-radius: 50px;
  padding: 3px 8px;
  font-size: 14px;
}

.hr-recent__categories a:hover {
  opacity: 0.7;
}

.hr-recent h4 {
  position: relative;
  pointer-events: none;
  color: var(--c-white);
  line-height: 1.3;
  margin-top: 15px;
  margin-bottom: 0;
  padding-left: 10%;
  font-size: 20px;
}

.hr-recent__title-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.hr-recent__title {
  margin-bottom: 0;
  font-size: 24px;
}

@media (max-width: 991px) {
  .hr-recent__title {
    font-size: 21px;
  }
}

.hr-recent__btn {
  font-size: 15px;
  border-bottom: 1px solid var(--c-blue);
}

.hr-recent__btn:hover {
  color: var(--c-blue);
}

.hr-call_to_action {
  padding: 50px 0 40px;
  background: linear-gradient(#fff, #9ab6f9);
}

.hr-call_to_action h1,
.hr-call_to_action h2,
.hr-call_to_action h3,
.hr-call_to_action h4,
.hr-call_to_action h5,
.hr-call_to_action h6 {
  color: var(--c-grey);
}

.hr-call_to_action h1 a,
.hr-call_to_action h2 a,
.hr-call_to_action h3 a,
.hr-call_to_action h4 a,
.hr-call_to_action h5 a,
.hr-call_to_action h6 a {
  color: var(--c-dark);
  text-decoration: underline;
}

.hr-call_to_action .wpcf7-form {
  direction: rtl;
}

.hr-call_to_action .wpcf7-form p {
  display: flex;
  margin-left: -10px;
  margin-right: -10px;
  flex-wrap: wrap;
  margin-bottom: 0;
  position: relative;
}

.hr-call_to_action .wpcf7-form br {
  display: none;
}

.hr-call_to_action .wpcf7-form input:not([type="submit"]) {
  width: 100%;
  height: 100%;
}

.hr-call_to_action .wpcf7-form .wpcf7-form-control-wrap,
.hr-call_to_action .wpcf7-form input[type="submit"] {
  flex: 0 0 calc(33.33% - 20px);
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.hr-call_to_action .wpcf7-form .ajax-loader {
  position: absolute;
  left: -25px;
  top: 15px;
}

.hr-call_to_action .wpcf7-form .wpcf7-not-valid-tip {
  font-size: 13px;
}

.hr-call_to_action .wpcf7-form div.wpcf7-response-output {
  font-size: 13px;
  margin: 20px 0 0;
  color: var(--c-dark);
}

@media (max-width: 767px) {
  .sidebar {
    order: 1;
  }
}

.sidebar .search-field {
  width: 100%;
}

.sidebar .widget_search label {
  display: block;
}

.sidebar .widget_search [type="search"] {
  padding-top: 10px;
  padding-bottom: 10px;
}

.sidebar .search-submit {
  display: none;
}

.sidebar .widget:not(:last-child) {
  margin-bottom: 25px;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar a {
  color: var(--c-grey);
}

.sidebar a:hover {
  color: var(--c-blue);
}

